export class AccountsTableComponent extends EntitiesComponent {
    constructor(accountService, store, dialog, snack, confirmService) {
        super(accountService);
        this.store = store;
        this.dialog = dialog;
        this.snack = snack;
        this.confirmService = confirmService;
        this.crumbs = [
            { name: 'Dashboard', link: '/dashboard' },
            { name: 'Grid', link: '/dashboard/grid' },
            { name: 'CRUD Table' },
        ];
        // readonly columns = [ { property: 'id'},{ property: 'name'},{ property: 'gender'},{ property: 'age'} ] as EntityColumnDef<Account>[]
        this.columns = [
            // prettier-ignore
            new EntityColumnDef({ property: 'userId', header: 'No.', displayFn: (entity) => `${entity.id}` }),
            // prettier-ignore
            // tslint:disable:max-line-length
            new EntityColumnDef({ property: 'Name', header: 'Name', displayFn: (entity) => `${entity.first_name} ${entity.last_name}` }),
            new EntityColumnDef({ property: 'gender', header: 'Gender' }),
            // prettier-ignore
            new EntityColumnDef({ property: 'dob', header: 'DoB', displayFn: (entity) => `${format(this.stringToDate(entity.dob), 'MMMM dd, yyyy')}` }),
            new EntityColumnDef({ property: 'city', header: 'City', displayFn: entity => `${entity.address.city}` }),
            new EntityColumnDef({
                property: 'state',
                header: 'State',
                displayFn: entity => `${entity.address.state}`,
            }),
        ];
        // optional
        this.showActionColumn = true;
        this.showColumnFilter = true;
        this.showToolbar = true;
        this.formRef = AccountEditComponent;
    }
    // optional
    delete(item) {
        return this.confirmService.confirm('Confirm', `Delete ${item.first_name} ${item.last_name}?`).pipe(filter(confirmed => confirmed === true), mergeMap(_ => super.delete(item)), tap(_ => {
            this.snack.open('Member Deleted!', 'OK', { duration: 5000 });
            this.store.dispatch(new Navigate([`/dashboard/grid/crud-table`]));
        }), catchError(error => {
            this.snack.open(error, 'OK', { duration: 10000 });
            return throwError('Ignore Me!');
        }));
    }
    // required to override
    getNewEntity() {
        const entity = new Account();
        entity.address = new Address();
        return entity;
    }
    // optional
    showDetails(entity) {
        if (entity) {
            this.store.dispatch(new Navigate([`/dashboard/grid/crud-table/${entity.id}`]));
        }
        else {
            this.store.dispatch(new Navigate(['/dashboard/grid/crud-table']));
        }
    }
    // filterPredicate(entity: Account, _filter: string): boolean  {
    //   return entity.first_name.indexOf(_filter) !== -1;
    // }
    /**
     *  openPopUp() is used in entity.component.html
     *  if you want different implantation (e.g., add-new-line instead of popup, inline edit)
     *  make a copy of entity.component.html as <entity>.component.html and implement your own add/edit logic.
     */
    openPopUp(entity) {
        let isNew = false;
        let id;
        if (!entity) {
            isNew = true;
            entity = this.getNewEntity();
        }
        else {
            id = entity.id;
        }
        const title = isNew ? 'Add Member' : 'Update Member';
        const dialogRef = this.dialog.open(this.formRef, {
            width: '720px',
            disableClose: true,
            data: { title, payload: entity },
        });
        dialogRef
            .afterClosed()
            .pipe(filter(res => res !== false), 
        // tap(res => console.log(res)),
        concatMap((res) => super.updateOrCreate(res, id)))
            .subscribe(_ => {
            this.snack.open(isNew ? 'Member Created!' : 'Member Updated!', 'OK', { duration: 5000 });
            this.store.dispatch(new Navigate([`/dashboard/grid/crud-table`]));
        }, error => this.snack.open(error, 'OK', { duration: 10000 }));
    }
}
